import React from 'react';
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { storage } from '../firebase';

import { Container, List, Typography, Button, ListItem, Link, IconButton } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';

function LessonHomework({ currentUser }) {
  const { userId, pupil, date } = useParams();
  const [metas, setMetas] = React.useState([]);
  const [urls, setUrls] = React.useState([]);

  let homeworkLink = null;

  const pupils = useSelector(({ pupils }) => pupils.items);
  const isLoaded = useSelector(({ pupils }) => pupils.isLoaded);
  if (isLoaded) homeworkLink = pupils[pupil].homeworkLink;

  React.useEffect(() => {
    // TODO: replace  arrays to objects
    const storageRef = storage.ref();
    const metasPromises = [];
    const urlPromises = [];
    storageRef
      .child(`${userId}/${pupil}/${date}`)
      .listAll()
      .then((res) =>
        res.items.forEach((item) => {
          urlPromises.push(item.getDownloadURL());
          metasPromises.push(item.getMetadata());
        }),
      )
      .then(() => {
        Promise.all(metasPromises).then((res) => setMetas(res))
        Promise.all(urlPromises).then((res) => setUrls(res));
      });
  }, []);

  async function handleSubmit(e) {
    const storageRef = storage.ref();

    const fileSnapshot = await storageRef
      .child(`${userId}/${pupil}/${date}/${e.currentTarget.files[0].name}`)
      .put(e.currentTarget.files[0]);

    setMetas((prev) => [...prev, fileSnapshot.metadata]);
    fileSnapshot.ref.getDownloadURL().then((url) => setUrls((prev) => [...prev, url]));
  }

  async function handleDelete(metadata) {
    const storageRef = storage.ref();
    console.log(metadata.fullPath);
    const fileRef = storageRef.child(metadata.fullPath);
    fileRef.delete();

    const index = metas.findIndex((item) => item.name === metadata.name);
    if (index >= 0) setUrls((prev) => prev.filter((item, i) => i !== index));
    setMetas((prev) => prev.filter((_, i) => i !== index));
  }

  return (
    <Container className="homework__lesson-container">
      {currentUser && (
        <Typography className="homework__lesson-title" variant="body1">
               : {homeworkLink}
        </Typography>
      )}
      <Button
        variant="contained"
        color="secondary"
        component="label"
        startIcon={<AddIcon />}
        className="lessons__button-text">
         
        <input type="file" onChange={handleSubmit} accept="" hidden />
      </Button>
      <List>
        {metas.map((meta, i) => (
          <ListItem key={`${meta.name}${i}`} className="homework__lesson-item">
            <Link href={urls[i]} target="_blank">
              <Typography>{meta.name}</Typography>
            </Link>
            <IconButton
              edge="end"
              aria-label=""
              onClick={() => {
                handleDelete(meta);
              }}>
              <DeleteIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>
    </Container>
  );
}

export default LessonHomework;
